
import { defineComponent, getCurrentInstance, toRefs } from "vue";
import BaoyanIcon from "@/components/layouts/BaoyanIcon.vue";
import mixin from "@/mixins/mixin";
import { Icon } from "@/enums";
import { HttpManager } from "@/api";

export default defineComponent({
  components: {
    BaoyanIcon,
  },
  props: {
    title: String,
    playList: Array,
    path: String,
  },
  setup(props) {
    const { proxy } = getCurrentInstance();
    const { routerManager, getImageUrl } = mixin();

    const { path } = toRefs(props);

    function goAblum(item) {
      if (path.value === "course-detail") {
        proxy.$store.commit("setCourseDetail", item);
      } else if (path.value === "guru-detail") {
        proxy.$store.commit("setGuruDetail", item);
      } else {
        // proxy.$store.commit("setPlayListDetail", item);
      }
      routerManager(path.value, { path: `/${path.value}/${item.id}` });
    }

    return {
      LIANHUA: Icon.LIANHUA,
      goAblum,
      attachImageUrl: HttpManager.attachImageUrl,
      getImageUrl
    };
  },
});
